<template>
  <div class='container'>
    <nut-navbar
      style="background-color:rgba(0,0,0,0.2); color: #fff; height:55px; line-height: 55px;"
        @on-click-back="NavBarLeft"
        :leftShow="true"
        :rightShow="false"
    >{{title}}</nut-navbar>
    <van-cell center title="编辑模式">
      <van-switch v-model="fix"  slot="right-icon" size="24" />
    </van-cell>
    <van-list v-if="!this.fix"
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell @click="jumpTo(`https://${item.url}`)" v-for="item in list" :key="item.index">
        <div class="cell">
          <div v-if="!item.cover.images.length" class="noImg">{{item.title}}</div>
          <van-image
          v-else
          width="60px"
          height="60px"
          :src='item.cover.images[0]'
          />
          <div class="cell_right">
            <div class="cell_text">{{item.title}}</div>
            <div class="cell_url"> {{`https://${item.url}`}}</div>
          </div>
        </div>
      </van-cell>
    </van-list>
    <van-list v-else
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
    >
      <van-cell @click="toEdit(item)" v-for="item in list" :key="item.index">
        <div class="cell">
          <div v-if="!item.cover.images.length" class="noImg">{{item.title}}</div>
          <van-image
          v-else
          width="60px"
          height="60px"
          :src='item.cover.images[0]'
          />
          <div class="cell_right">
            <div class="del" @click.stop="delArticles(item.id)">+</div>
            <div class="cell_text">{{item.title}}</div>
            <div class="cell_url"> {{`https://${item.url}`}}</div>
          </div>
        </div>
      </van-cell>
      <van-cell v-if="this.fix">
        <div class="plus" @click="toPublish">新增</div>
      </van-cell>
    </van-list>
    <van-popup v-model="show" @close="id = null">
      <div class="publish">
        <van-field
          v-model="content.title"
          name="简介标题"
          label="简介标题"
          placeholder="请填写简介标题（至少5个字）"
          :rules="[{ required: true, message: '请填写简介标题' }]"
        />
        <van-field
          v-model="content.url"
          name="地址"
          label="网络地址"
          placeholder="请填写网址"
          :rules="[{ required: true, message: '请填写网址' }]"
        />
        <div style="margin-top: 16px;display:flex">
           <nut-button
              v-if="!this.id"
              @click="publishArticles"
              style="background-color:rgba(0,0,0,0.1);font-size : 16px"
              block
              color="#000"
              type="light"
            >
            确定
           </nut-button>
           <nut-button
              v-else
              @click="editArticles"
              style="background-color:rgba(0,0,0,0.1);font-size : 16px"
              block
              color="#000"
              type="light"
            >
            修改
           </nut-button>
           <nut-button
              @click="cancel"
              style="background-color:rgba(0,0,0,0.1);font-size : 16px"
              block
              color="#000"
              type="light"
            >
            取消
           </nut-button>
        </div>
      </div>
    </van-popup>
    <van-pagination
      v-model="currentPage"
      :page-count='totalPage'
      mode="simple"
    />
  </div>
</template>

<script>
import { EditArticles, DelArticles, GetArticles, PublishArticles } from '@/request/api'
export default {
  data () {
    return {
      totalPage: 0,
      currentPage: 1,
      channelId: null,
      timestamp: Date.now(),
      id: null,
      show: false,
      fix: false,
      list: [],
      title: '标题',
      loading: false,
      finished: false,
      content: {
        url: '',
        title: ''
      },
      publish: {
        title: '',
        content: '',
        cover: {
          type: 0,
          images: []
        },
        channel_id: 4
      }
    }
  },
  methods: {
    cancel () {
      this.show = false
      this.timestamp = Date.now()
    },
    toEdit (item) {
      this.show = true
      this.content.title = item.title
      this.content.url = item.url
      this.id = item.id
    },
    del (e) {
      this.list.forEach(function (item, index, arr) {
        if (item.id === e) {
          arr.splice(index, 1)
        }
      })
    },
    jumpTo (e) {
      window.open(e)
    },
    showPopup () {
      this.fix = true
    },
    NavBarLeft () {
      this.$router.push('Home')
    },
    NavBarRight () {
      this.showPopup()
    },
    onLoad () {
      this.loading = false
      // 异步更新数据
      // setTimeout 仅做示例，真实场景中一般为 ajax 请求
      // setTimeout(() => {
      //   for (let i = 0; i < 10; i++) {
      //     this.list.push(this.list.length + 1)
      //   }

      //   // 加载状态结束
      //   this.loading = false

      //   // 数据全部加载完成
      //   if (this.list.length >= 10) {
      //     this.finished = true
      //   }
      // }, 1000)
    },
    async delArticles (id) {
      DelArticles(id)
      this.$toast.fail('删除成功')
      this.del(id)
    },
    async getArticles () {
      const data = await GetArticles({ page: this.currentPage })
      data.results.forEach(function (item, index, arr) {
        item.url = item.title.split('<hr>')[1]
        item.title = item.title.split('<hr>')[0]
      })
      this.list = data.results
      this.totalPage = Math.ceil(data.total_count / 10)
    },
    editArticles () {
      this.publish.id = this.id
      this.publish.title = `${this.content.title}<hr>${this.content.url}`
      this.publish.content = '这里是草稿'
      EditArticles(this.publish, true)
      setTimeout(() => {
        this.cancel()
        this.$toast.success('修改成功')
      }, 1000)
    },
    publishArticles () {
      this.publish.title = `${this.content.title}<hr>${this.content.url}`
      this.publish.content = '这里是草稿'
      PublishArticles(this.publish, true)
      setTimeout(() => {
        this.cancel()
        this.$toast.success('操作成功')
      }, 1000)
    },
    toPublish () {
      this.show = true
    }
  },
  created () {
    this.getArticles()
  },
  watch: {
    currentPage () {
      this.getArticles()
    },
    timestamp () {
      this.getArticles()
    }
  }
}
</script>

<style lang='less'>
.van-pagination {
 background-color: #fff;
}
.publish{
  width:375px;
}
.plus{
  text-align: center;
  border: 1px solid #ccc;
}
.cell{
  display: flex;
  position: relative;
  height: 70px;
  padding: 5px;
  box-sizing: border-box;
  .noImg{
    overflow: hidden;
    padding: 5px;
    box-sizing: border-box;
    width: 60px;
    height: 60px;
    line-height: 48px;
    text-align: center;
    border: 1px solid #ccc;
    font-size: 18px;
  }
  .cell_right{
    .del{
      color: red;
      font-size: 30px;
      transform:rotate(45deg);
      position: absolute;
      top: 5px;
      right: 5px;
    }
    padding: 5px;
    padding-left: 30px;
    box-sizing: border-box;
    .cell_text{
      height: 32px;
      box-sizing: border-box;
      padding-bottom: 5px;
    }
    .cell_url{
      font-size: 12px;
      color:blue;
    }
  }
}
</style>
